// Media queries mixins - responsive breakpoints utilities
//   -     _variables.scss

//      
@import '../variables/variables';

// ============================================
// Mobile-first approach (min-width) - "  "
// ============================================

//  xs   (375px+)
@mixin xs-up {
  @media (min-width: $breakpoint-mobile) {
    @content;
  }
}

//  sm   (480px+)
@mixin sm-up {
  @media (min-width: $breakpoint-sm) {
    @content;
  }
}

//  md   (576px+)
@mixin md-up {
  @media (min-width: $breakpoint-md) {
    @content;
  }
}

//  lg   (768px+)
@mixin lg-up {
  @media (min-width: $breakpoint-lg) {
    @content;
  }
}

//  xl   (992px+)
@mixin xl-up {
  @media (min-width: $breakpoint-xl) {
    @content;
  }
}

//  xxl   (1440px+)
@mixin xxl-up {
  @media (min-width: $breakpoint-xxl) {
    @content;
  }
}

// ============================================
// Desktop-first approach (max-width) - "  "
// ============================================

//  xs ( 375px)
@mixin xs-down {
  @media (max-width: ($breakpoint-mobile - 1px)) {
    @content;
  }
}

//  sm ( 480px)
@mixin sm-down {
  @media (max-width: ($breakpoint-sm - 1px)) {
    @content;
  }
}

//  md ( 576px)
@mixin md-down {
  @media (max-width: ($breakpoint-md - 1px)) {
    @content;
  }
}

//  lg ( 768px) -  
@mixin lg-down {
  @media (max-width: ($breakpoint-lg - 1px)) {
    @content;
  }
}

//  xl ( 992px)
@mixin xl-down {
  @media (max-width: ($breakpoint-xl - 1px)) {
    @content;
  }
}

//  xxl ( 1440px)
@mixin xxl-down {
  @media (max-width: ($breakpoint-xxl - 1px)) {
    @content;
  }
}

// ============================================
//   (only) - "  "
// ============================================

//  xs ( 480px)
@mixin xs-only {
  @media (max-width: ($breakpoint-sm - 1px)) {
    @content;
  }
}

//  sm ( 480px  576px)
@mixin sm-only {
  @media (min-width: $breakpoint-sm) and (max-width: ($breakpoint-md - 1px)) {
    @content;
  }
}

//  md ( 576px  768px)
@mixin md-only {
  @media (min-width: $breakpoint-md) and (max-width: ($breakpoint-lg - 1px)) {
    @content;
  }
}

//  lg ( 768px  992px)
@mixin lg-only {
  @media (min-width: $breakpoint-lg) and (max-width: ($breakpoint-xl - 1px)) {
    @content;
  }
}

//  xl ( 992px  1440px)
@mixin xl-only {
  @media (min-width: $breakpoint-xl) and (max-width: ($breakpoint-xxl - 1px)) {
    @content;
  }
}

//  xxl ( 1440px  )
@mixin xxl-only {
  @media (min-width: $breakpoint-xxl) {
    @content;
  }
}

// ============================================
// Legacy aliases (  )
// ============================================

// Mobile-first ( )
@mixin mobile-up {
  @include xs-up {
    @content;
  }
}

@mixin tablet-up {
  @include lg-up {
    @content;
  }
}

@mixin desktop-up {
  @include xxl-up {
    @content;
  }
}

// Desktop-first ( )
@mixin mobile-only {
  @include lg-down {
    @content;
  }
}

@mixin mobile-down {
  @include lg-down {
    @content;
  }
}

@mixin tablet-only {
  @include lg-only {
    @content;
  }
}

@mixin tablet-down {
  @include xxl-down {
    @content;
  }
}

@mixin desktop-only {
  @include xxl-only {
    @content;
  }
}

//   
@mixin mobile {
  @include lg-down {
    @content;
  }
}

@mixin tablet {
  @include lg-only {
    @content;
  }
}

@mixin desktop {
  @include xxl-only {
    @content;
  }
}

// ============================================
//     
// ============================================

//   
@mixin between($min, $max: null) {
  @if $max == null {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @media (min-width: $min) and (max-width: ($max - 1px)) {
      @content;
    }
  }
}

//  min-width
@mixin min-width($width) {
  @media (min-width: $width) {
    @content;
  }
}

//  max-width
@mixin max-width($width) {
  @media (max-width: ($width - 1px)) {
    @content;
  }
}

// ============================================
//  
// ============================================

@mixin landscape {
  @media (orientation: landscape) {
    @content;
  }
}

@mixin portrait {
  @media (orientation: portrait) {
    @content;
  }
}

// ============================================
// High DPI / Retina displays
// ============================================

@mixin retina {
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
    @content;
  }
}

@mixin high-dpi($ratio: 2) {
  @media (-webkit-min-device-pixel-ratio: $ratio), (min-resolution: #{$ratio * 96}dpi) {
    @content;
  }
}

// ============================================
// Print media
// ============================================

@mixin print {
  @media print {
    @content;
  }
}
